@font-face {
  font-family: "OpenGraph";
  font-weight: 500;
  font-style: normal;
  font-display: swap;
  src: url("/fonts/text-medium.woff") format("woff");
}
@font-face {
  font-family: "OpenGraph";
  font-weight: 900;
  font-style: normal;
  font-display: swap;
  src: url("/fonts/text-black.woff") format("woff");
}
@font-face {
  font-family: "OpenGraph";
  font-weight: 400;
  font-style: normal;
  font-display: swap;
  src: url("/fonts//text-regular.woff") format("woff");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.page {
  min-width: 375px;
  height: 100%;
  font-family: "OpenGraph", sans-serif;
  font-size: 16px;
  line-height: 120%;
}
@media (min-width: 768px) {
  .page {
    font-size: 24px;
    line-height: 28px;
  }
}

.page__body {
  min-height: 100%;
  margin: 0;
  display: grid;
  grid-template-rows: min-content 1fr min-content;
  background-color: #FFFFFF;
}

.page-main__header {
  background-repeat: no-repeat;
}
@media (max-width: 767px) {
  .page-main__header {
    position: relative;
    padding: 32px 0 350px;
    background-size: 354px 308px, cover, cover;
    background-position: 50% 310px, 100% 100%, 100% 100%;
    background-repeat: no-repeat, repeat, repeat;
    background: url("../img/promo-small.png"), linear-gradient(180deg, rgba(255, 255, 255, 0) 0%, #FFFFFF 100%), linear-gradient(to right, rgba(255, 92, 77, 0.2), rgba(235, 70, 159, 0.2), rgba(131, 65, 239, 0.2), rgba(63, 104, 249, 0.2));
  }
}
@media (max-width: 767px) and (min-resolution: 2dppx) {
  .page-main__header {
    background: url("../img/promo-small@2x.png"), linear-gradient(180deg, rgba(255, 255, 255, 0) 0%, #FFFFFF 100%), linear-gradient(to right, rgba(255, 92, 77, 0.2), rgba(235, 70, 159, 0.2), rgba(131, 65, 239, 0.2), rgba(63, 104, 249, 0.2));
    background-size: 354px 308px, cover, cover;
    background-position: 50% 310px, 100% 100%, 100% 100%;
    background-repeat: no-repeat, repeat, repeat;
  }
}
@media (min-width: 768px) {
  .page-main__header {
    padding: 124px 0 100px;
    background: url("../img/promo-big.png"), linear-gradient(180deg, rgba(255, 255, 255, 0) 0%, #FFFFFF 100%), linear-gradient(90deg, rgba(255, 92, 77, 0.2), rgba(235, 70, 159, 0.2), rgba(131, 65, 239, 0.2), rgba(63, 104, 249, 0.2));
    background-size: 859px auto, cover, cover;
    background-position: 60% 0%, 100% 100%, 100% 100%;
    background-repeat: no-repeat, repeat, repeat;
  }
}
@media (min-width: 768px) and (min-resolution: 2dppx) {
  .page-main__header {
    background: url("../img/promo-big@2x.png"), linear-gradient(180deg, rgba(255, 255, 255, 0) 0%, #FFFFFF 100%), linear-gradient(90deg, rgba(255, 92, 77, 0.2), rgba(235, 70, 159, 0.2), rgba(131, 65, 239, 0.2), rgba(63, 104, 249, 0.2));
    background-size: 859px auto, cover, cover;
    background-position: 60% 0%, 100% 100%, 100% 100%;
    background-repeat: no-repeat, repeat, repeat;
  }
}

.page-main__heading {
  margin: 0;
  margin-bottom: 35px;
  font-family: "OpenGraph", sans-serif;
  font-size: 36px;
  font-weight: 900;
  line-height: 100%;
  letter-spacing: -2px;
}
@media (min-width: 768px) {
  .page-main__heading {
    margin-bottom: 48px;
    font-size: 72px;
    text-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
  }
}

.page-main__subheading {
  margin-top: 0;
  margin-bottom: 32px;
  font-family: "OpenGraph", sans-serif;
  font-size: 32px;
  line-height: 100%;
  font-weight: 900;
  letter-spacing: -2px;
}
@media (min-width: 768px) {
  .page-main__subheading {
    margin-bottom: 48px;
    font-size: 48px;
    line-height: 48px;
  }
}

.wrapper {
  width: 311px;
  margin-right: auto;
  margin-left: auto;
}
@media (min-width: 768px) {
  .wrapper {
    width: 960px;
  }
}

.logo {
  display: block;
}
@media (min-width: 768px) {
  .logo {
    height: 50px;
    width: 176px;
  }
}

.button {
  padding: 13.5px;
  display: inline-block;
  background-color: #FFDE40;
  color: #21201F;
  border-radius: 36px;
  text-decoration: none;
  text-align: center;
  font-size: 18px;
  font-weight: 500px;
  line-height: 22px;
  box-shadow: 0 4px 64px 0 rgba(255, 222, 64, 0.59);
}
@media (min-width: 768px) {
  .button {
    padding: 21.5px 48px;
    font-size: 24px;
    line-height: 22px;
  }
}

.button:hover {
  box-shadow: 0 4px 84px 0 rgba(250, 138, 63, 0.59);
}

@media (max-width: 767px) {
  .promo__wrapper {
    padding-bottom: 350px;
  }
}
@media (min-width: 768px) {
  .promo__wrapper {
    padding-bottom: 125px;
  }
}

.promo__text {
  margin-top: 0;
  margin-bottom: 30px;
  font-size: 18px;
  line-height: 130%;
}
@media (min-width: 768px) {
  .promo__text {
    width: 420px;
    margin-bottom: 44px;
    font-size: 24px;
  }
}

@media (max-width: 767px) {
  .promo__button {
    position: absolute;
    top: 620px;
    width: 311px;
  }
}
@media (min-width: 768px) {
  .promo__button {
    width: auto;
    justify-self: start;
  }
}

.promo__old-price {
  display: inline-block;
  padding: 5px 7px;
  padding-left: 0;
  color: #808080;
  font-size: 13px;
  line-height: 16px;
  border-radius: 36px;
  background: rgba(224, 184, 255, 0.6);
}
@media (min-width: 768px) {
  .promo__old-price {
    font-size: 16px;
    line-height: 19px;
    padding: 8px 12px;
    padding-left: 0;
  }
}

.promo__new-price {
  color: #21201F;
  font-size: 18px;
  line-height: 22px;
  font-weight: 500;
  background: radial-gradient(100% 100% at 100% 0%, #E0B8FF 0%, #B6B9FF 100%), linear-gradient(0deg, rgba(241, 241, 241, 0.6), rgba(241, 241, 241, 0.6));
  padding: 5px 7px;
  border-radius: 36px;
  text-decoration: none;
}
@media (min-width: 768px) {
  .promo__new-price {
    font-size: 24px;
    line-height: 29px;
    padding: 8px 12px;
  }
}

.page-header__logo {
  display: block;
  margin-bottom: 32px;
}
@media (min-width: 768px) {
  .page-header__logo {
    margin-bottom: 72px;
  }
}

.page-footer {
  padding: 32px 32px 72px;
}
@media (min-width: 768px) {
  .page-footer {
    padding-top: 21px;
    padding-bottom: 128px;
  }
}

.page-footer__text {
  margin: 0;
  color: #C5C5C5;
  font-size: 10px;
  font-weight: 400;
  line-height: 120%;
}
@media (min-width: 768px) {
  .page-footer__text {
    font-size: 13px;
  }
}

.advantages {
  padding: 32px;
  padding-bottom: 40px;
}
@media (min-width: 768px) {
  .advantages {
    padding-bottom: 98px;
  }
}

@media (min-width: 768px) {
  .advantages__header {
    font-size: 56px;
    line-height: 56px;
  }
}

@media (max-width: 767px) {
  .advantages__header br {
    display: none;
  }
}

.advantages__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
}
@media (min-width: 768px) {
  .advantages__list {
    display: grid;
    grid-template-columns: 362px 400px;
    column-gap: 112px;
    row-gap: 61px;
  }
}

.advantages__item {
  display: grid;
  grid-template-columns: 83px 1fr;
  gap: 16px;
}
@media (max-width: 767px) {
  .advantages__item {
    align-items: center;
  }
}
@media (min-width: 768px) {
  .advantages__item {
    grid-template-columns: auto;
    grid-template-rows: 83px auto;
  }
}
@media (max-width: 767px) {
  .advantages__item + .advantages__item {
    margin-top: 24px;
  }
}
.advantages__item::before {
  content: "";
  width: 83px;
  height: 83px;
  background-repeat: no-repeat;
  background-position: 50% 50%;
}
@media (min-width: 768px) {
  .advantages__item {
    margin-top: 0;
  }
}

.advantages__item--price::before {
  background-image: url("../img/rub-icon.svg");
}

.advantages__item--friends::before {
  background-image: url("../img/scooter-icon.svg");
}

.advantages__item--time::before {
  background-image: url("../img/time-icon.svg");
}

.advantages__item--energy::before {
  background-image: url("../img/battery-icon.svg");
}

.benefits {
  position: relative;
  padding: 16px;
  padding-bottom: 40px;
}
@media (min-width: 768px) {
  .benefits {
    padding-bottom: 10px;
  }
}

@media (max-width: 767px) {
  .benefits__wrapper {
    width: 343px;
  }
}

.benefits-photo__item::before {
  position: absolute;
  z-index: 5;
  content: "";
  display: block;
  background-repeat: no-repeat;
  background-position: center;
}
@media (max-width: 767px) {
  .benefits-photo__item::before {
    display: none;
  }
}

.benefits-photo__item--pearl::before {
  transform: translateX(20px);
  top: -50px;
  right: 0;
  height: 178px;
  width: 154px;
  background-image: url("../img/pearl.svg");
}

.benefits-photo__item--geo::before {
  top: 0px;
  left: -150px;
  width: 320px;
  height: 320px;
  background-image: url("../img/geo-icon.svg");
}

.benefits-photo__item--sweets::before {
  right: -90px;
  bottom: -200px;
  width: 300px;
  height: 400px;
  background-image: url("../img/cup-icon.svg");
}

.benefits-photo__list {
  position: relative;
  list-style-type: none;
  margin-top: 0;
  padding: 0;
}
@media (min-width: 768px) {
  .benefits-photo__list {
    display: grid;
    grid-template-columns: 455px 455px;
    grid-auto-rows: 455px;
    row-gap: 8px;
    column-gap: 16px;
    margin-bottom: 0;
  }
}

@media (max-width: 767px) {
  .benefits-photo__list::before {
    content: "";
    position: absolute;
    right: -50px;
    top: -70px;
    z-index: 5;
    display: block;
    width: 178px;
    height: 140px;
    background-image: url("../img/pearl.svg");
  }
}

.benefits-photo__item {
  position: relative;
  width: 343px;
  height: 343px;
}
@media (min-width: 768px) {
  .benefits-photo__item {
    width: 455px;
    height: 455px;
  }
}
@media (max-width: 768px) {
  .benefits-photo__item:not(:first-child) {
    display: none;
  }
}

@media (min-width: 768px) {
  .benefits-photo__item:nth-child(2n) {
    transform: translateY(-180px);
  }
}

.benefits-photo__image {
  display: block;
  width: 343px;
  height: 343px;
}
@media (min-width: 768px) {
  .benefits-photo__image {
    width: 455px;
    height: 455px;
  }
}

.benefits__text {
  margin-top: 0;
  margin-bottom: 48px;
}
@media (min-width: 768px) {
  .benefits__text {
    width: 442px;
  }
}

.slider__controls {
  display: flex;
  justify-content: center;
}
@media (min-width: 768px) {
  .slider__controls {
    display: none;
  }
}

.slider__button {
  width: 48px;
  height: 48px;
  background-color: #FFDE40;
  border-radius: 50%;
  border: none;
  cursor: pointer;
  background-image: url("../img/arrow-icon.svg");
  background-position: center;
  background-repeat: no-repeat;
}

.slider__button:hover {
  opacity: 0.5;
}

.button-prev {
  transform: rotate(180deg);
}

.slider__button + .slider__button {
  margin-left: 12px;
}

.cashback {
  padding: 16px 16px 40px;
}
@media (min-width: 768px) {
  .cashback {
    padding-top: 110px;
    position: relative;
    background-repeat: no-repeat;
    background-position: 75% -90px;
    background-image: url("../img/full-colored-scooter.png");
  }
}
@media (min-width: 768px) and (min-resolution: 2dppx) {
  .cashback {
    background-image: url("../img/full-colored-scooter@2x.png");
    background-size: auto 742px;
  }
}

@media (min-width: 768px) {
  .cashback__header {
    width: 407px;
  }
}

.cashback__text {
  margin: 0;
}
@media (min-width: 768px) {
  .cashback__text {
    width: 419px;
  }
}

.cashback__image {
  position: absolute;
  top: -200px;
  right: 300px;
}

@media (min-width: 768px) {
  .cashback__container {
    position: relative;
    width: 407px;
  }
}

.join-us {
  padding: 32px;
  padding-bottom: 40px;
  background-repeat: no-repeat;
}
@media (max-width: 767px) {
  .join-us {
    padding-top: 250px;
    background-image: url("../img/round-small-true.png");
    background-position-x: center;
    background-position-y: -20px;
  }
}
@media (max-width: 767px) and (min-resolution: 2dppx) {
  .join-us {
    background-image: url("../img/round-small-true@2x.png");
    background-size: auto 301px;
  }
}
@media (min-width: 768px) {
  .join-us {
    padding-top: 98px;
    padding-bottom: 90px;
    background-image: url("../img/round-large-true.png");
    background-position: 23% 10%;
  }
}
@media (min-width: 768px) and (min-resolution: 2dppx) {
  .join-us {
    background-image: url("../img/round-large-true@2x.png");
    background-size: auto 600px;
  }
}

.join-us__image {
  display: block;
  width: 301px;
  height: 347px;
}
@media (min-width: 768px) {
  .join-us__image {
    width: 450px;
    height: 456px;
  }
}

@media (min-width: 768px) {
  .join-us__wrapper {
    display: grid;
    grid-template-columns: 484px 419px;
    column-gap: 57px;
  }
}

.join-us__container {
  position: relative;
}

.join-us__text {
  margin-top: 0;
  margin-bottom: 32px;
}
@media (min-width: 768px) {
  .join-us__text {
    margin-bottom: 48px;
  }
}

.join-us__button {
  width: 100%;
}
@media (min-width: 768px) {
  .join-us__button {
    width: auto;
  }
}